<template>
  <div class="cloud-container" @click="moveCloud" :style="translateXY">
    <img src="../assets/cloud.png" alt="flyingCloud" width="100" />
    <p class="in-image">Click/Touch Me !</p>
  </div>
</template>

<script>
export default {
  data: function () {
    return {
      windowHeight: window.innerHeight,
      windowWidth: window.innerWidth,
      x: 0,
      y: 0,
      cloudInterval: null,
      cloudRunning: false,
    };
  },
  computed: {
    translateXY() {
      return { transform: "translate(" + this.x + "px, " + this.y + "px)" };
    },
  },
  methods: {
    moveCloud() {
      if (!this.cloudRunning) {
        let randomX = (Math.random() - 0.4) * (this.windowWidth * 0.2);
        let randomY = (Math.random() - 0.4) * (this.windowHeight * 0.2);
        let round = 0;

        if (this.x + randomX > this.windowWidth - 100 || this.x + randomX < 50) {
          randomX = -randomX;
        }
        if (this.y + randomY > this.windowHeight - 100 || this.y + randomY < 50) {
          randomY = -randomY;
        }

        this.cloudInterval = setInterval(() => {
          this.cloudRunning = true;
          this.x += randomX / 100;
          this.y += randomY / 100;
          round++;
          if (round > 100) {
            clearInterval(this.cloudInterval);
            this.cloudRunning = false;
          }
        }, 1);
      }
    },
  },
  mounted() {
    const randomNumber = Math.random() * (0.8-0.2) + 0.2;
    this.x = this.windowWidth * randomNumber;
    this.y = this.windowHeight * randomNumber;
  }
};
</script>

<style scoped>
.cloud-container {
  position: fixed;
  top: 0;
  z-index: 5;
}
.in-image {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 10px;
  text-align: center;
  font-family: "Caveat", cursive;
}
</style>