<template>
  <div style="padding: 0 10px">
    <h2>Transform your negative word to a more positive word</h2>
    <v-row justify="center">
      <h3>The word you consistently select will shape your destiny</h3>
    </v-row>
    <v-row justify="center">
      <v-col class="d-flex" :class="[smallerDevices? 'justify-center' : 'justify-end']" cols="12" md="12" lg="2">
        <p style="font-size: 20px">I'm feeling</p>
      </v-col>
      <v-col cols="12" md="12" lg="4">
        <v-menu open-on-hover offset-y>
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              block
              x-large
              rounded
              class="negative"
              dark
              v-bind="attrs"
              v-on="on"
            >
              {{ chosenWord }}
            </v-btn>
          </template>
          <v-list>
            <v-list-item v-for="(item, index) in words" :key="index">
              <v-list-item-title @click="setDisplayItem(item)">
                {{ item.negative }}
              </v-list-item-title>
            </v-list-item>
          </v-list>
        </v-menu>
      </v-col>
      <v-col v-if="smallerDevices" class="d-flex justify-center" justify="center">
        <v-icon large color="red"> mdi-arrow-down-bold </v-icon>
      </v-col>
      <v-col v-else class="d-flex justify-center" justify="center">
        <v-icon large color="red"> mdi-arrow-right-bold </v-icon>
      </v-col>
      <v-col class="d-flex justify-center" cols="12" md="12" lg="4">
        <v-btn block rounded class="positive" light x-large>
          {{ displayWord }}
        </v-btn>
      </v-col>
    </v-row>
    <v-row justify="center"
      ><p>From the book: [Anthony Robbins: Awaken the Giant Within]</p></v-row
    >
  </div>
</template>

<script>
export default {
  data() {
    return {
      chosenWord: ". . .",
      displayWord: "",
      words: [
        { negative: "Afraid", positive: "Uncomfortable" },
        { negative: "Anxious", positive: "A little concerned" },
        { negative: "Confused", positive: "Curious" },
        { negative: "Dread", positive: "Challenge" },
        { negative: "Embarrassed", positive: "Aware" },
        { negative: "Failure", positive: "Learning" },
        { negative: "Furious", positive: "Passionate" },
        { negative: "Insecure", positive: "Questioning" },
        { negative: "Insulted", positive: "Misinterpreted" },
      ],
    };
  },
  methods: {
    setDisplayItem(word) {
      this.displayWord = word.positive;
      this.chosenWord = word.negative;
    },
  },
  computed: {
    smallerDevices() {
      let breakpoint = this.$vuetify.breakpoint.name;
      if (breakpoint === "xs" || breakpoint === "sm" || breakpoint === "md")
        return true;
      return false;
    },
  },
};
</script>

