import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../pages/Home'
import Rule from '../pages/Rule'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/rule',
    name: 'Rule',
    component: Rule
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
