      <v-row>
        <h2 style="padding-bottom: 15px">Add Revenue Group</h2>
      </v-row>
      <v-row>
        <h3 style="padding-bottom: 5px">Revenue Group Tile</h3>
      </v-row>
      <v-row>
        <v-text-field outlined value="Fashion - 15%" dense></v-text-field>
      </v-row>
      <v-row
        align="center"
        class="d-flex align-center justify-start"
        style="margin-top: 0"
      >
        <p>If</p>
        <div style="width: 200px; margin: 15px 10px 5px">
          <v-select :items="items" outlined dense value="ALL"></v-select>
        </div>
        <p>of the below conditions are met</p>
      </v-row>
      <v-row v-for="row in rows" :key="row.id" class="grey-background">
        <v-row align="start" align-content="start" v-if="rows.length > 1">
          <div style="margin: 0px 0 20px 0px">
            <h4>RULE {{ row.id }}</h4>
          </div>
        </v-row>
        <v-row align="start" align-content="start">
          <div class="centering">
            <v-select
              :items="items"
              outlined
              dense
              value="ALL"
              background-color="white"
            ></v-select>
          </div>
          <div class="centering">
            <v-select
              :items="items"
              outlined
              dense
              value="ALL"
              background-color="white"
            ></v-select>
          </div>
          <div>
            <div v-for="rule in row.rules" :key="rule">
              <v-text-field
                outlined
                dense
                label="insert parameter"
                background-color="white"
                style="width: 665px"
                ><template v-slot:append>
                  <button
                    @click="addRule(row.id)"
                    class="link"
                    style="color: blue"
                    v-if="rule === row.rules[row.rules.length - 1]"
                  >
                    add rule
                  </button>
                  <button
                    @click="removeRule(row.id, rule)"
                    v-else
                    class="link"
                    style="color: red"
                  >
                    remove rule
                  </button>
                </template>
              </v-text-field>
            </div>
          </div>
          <div style="padding: 5px 8px 28px 8px">
            <v-icon @click="removeRow(row.id)" color="#b0b0b0"
              >mdi-minus-circle-outline</v-icon
            >
          </div>
          <div style="padding: 5px 8px 28px 0">
            <v-icon @click="addRow" v-if="row === rows[rows.length - 1]"
              >mdi-plus-circle-outline</v-icon
            >
            <v-icon v-else color="invisible" disabled style="opacity: 0"
              >mdi-plus-circle-outline</v-icon
            >
          </div>
        </v-row>
      </v-row>

      <v-row align="center" class="d-flex align-center justify-start">
        <p>then revenue is</p>
        <div style="width: 200px; margin: 15px 10px 5px">
