</template>

<script>
export default {
  data() {
    return {
      items: ["ALL", "Foo", "Bar", "Fizz", "Buzz"],
      rows: [
        {
          id: 1,
          rules: ["1"],
        },
      ],
    };
  },

  methods: {
    addRow() {
      const id = this.rows[this.rows.length - 1].id;
      this.rows.push({
        id: id + 1,
        rules: ["1"],
      });
    },
    removeRow(id) {
      if (this.rows.length > 1) {
        this.rows = this.rows.filter((el) => {
          return el.id !== id;
        });
      }
    },
    addRule(id) {
      const row = this.rows.find((el) => {
        return el.id === id;
      });
      row.rules.push(Date.now());
    },
    removeRule(id, rule) {
      const row = this.rows.find((el) => {
        return el.id === id;
      });
      console.log(row.rules);
      if (row.rules.length > 1) {
        row.rules = row.rules.filter((el) => {
          return el !== rule;
        });
      }
    },
  },
  computed: {},
};
</script>

